import React, { Component } from 'react'
import './App.css';
import Display from './components/Display';
import Buttons from './components/Buttons';

const isOperator = /[-/*+]/,
  endsWithOperator = /[+-/*]$/,
  endsWithNegativeSign = /\d[x/+‑]{1}‑$/

export default class App extends Component {
  constructor(props){
    super(props);
    this.state = {
      prevVal: '0',
      currVal: '0',
      formula: '',
      operation: 'pos',
      evaluated: false
    };
    this.maxDigitWarning = this.maxDigitWarning.bind(this);
    this.handleOperators = this.handleOperators.bind(this);
    this.handleEvaluate = this.handleEvaluate.bind(this);
    this.initialize = this.initialize.bind(this);
    this.handleDecimal = this.handleDecimal.bind(this);
    this.handleNumbers = this.handleNumbers.bind(this);
  }
maxDigitWarning(){
  this.setState({
    currVal: 'Digit Limit Met',
    prevVal: this.state.currVal
  });
  setTimeout(() => this.setState({currVal: this.state.prevVal}), 1000)

  }
clickEffect(e){
  e.target.classList.add('button-active');
  setTimeout(() => e.target.classList.remove('button-active'), 200);
}
initialize(e){
  this.clickEffect(e);
    this.setState({
      currVal: '0',
      prevVal: '0',
      formula: '',
      evaluated: false
    });
}
handleNumbers(e){
  this.clickEffect(e);
  if (!this.state.currVal.includes('Limit')) {
  const {currVal, formula, evaluated} = this.state;
  const value = e.target.value;
  this.setState({evaluated: false});
  if (currVal.length > 21) {
    this.maxDigitWarning();
  } else if (evaluated) {
    this.setState({
      currVal: value,
      formula: value !== '0' ? value : ''
    });
  } else {
    this.setState({
      currVal: currVal === '0' || isOperator.test(currVal)
      ? value
      : currVal + value,
      formula:
      currVal === '0' && value === '0'
      ? formula === ''
      ? value
      : formula : /([^.0-9]0|^0)$/.test(formula)
      ? formula.slice(0, -1) + value
      : formula + value
    })
  }
  }
  };
handleOperators(e){
  this.clickEffect(e);
  if(!this.state.currVal.includes('Limit')) {
    const value = e.target.value;
    const {formula, prevVal, evaluated} = this.state;
    this.setState({currVal: value, evaluated: false});
    if (evaluated) {
      this.setState({formula: prevVal + value});
    } else if (!endsWithOperator.test(formula)) {
      this.setState({
        prevVal: formula,
        formula: formula + value
      });
    } else if (!endsWithNegativeSign.test(formula)) {
      this.setState({
        formula: (endsWithNegativeSign.test(formula + value)
        ? formula : prevVal) + value
      });
    } else if (value !== '-') {
      this.setState({
        formula: prevVal + value
      });
    }
  }
}
handleEvaluate(e) {
  this.clickEffect(e);
    if (!this.state.currVal.includes('Limit')) {
      let expression = this.state.formula;
      while(endsWithOperator.test(expression)){
        expression = expression.slice(0, -1);
      }
      expression = expression.replace('--', '+').replace(/×/g, '*')
      let answer = Math.round(1000000000000 * eval(expression)) / 1000000000000;
      this.setState({
        currVal: answer.toString(),
        formula: expression + '=' + answer,
        prevVal: answer,
        evaluated: true
      });
    }
}

handleDecimal(e) {
  this.clickEffect(e);
  if (this.state.evaluated === true) {
    this.setState({
      currVal: '0.',
      formula: '0.',
      evaluated: false
    });
  } else if (
    !this.state.currVal.includes('.') &&
    !this.state.currVal.includes('Limit')
  ) {
    this.setState({ evaluated: false });
    if (this.state.currVal.length > 21) {
      this.maxDigitWarning();
    } else if (
      endsWithOperator.test(this.state.formula) ||
      (this.state.currVal === '0' && this.state.formula === '')
    ) {
      this.setState({
        currVal: '0.',
        formula: this.state.formula + '0.'
      });
    } else {
      this.setState({
        currVal: this.state.formula.match(/(-?\d+\.?\d*)$/)[0] + '.',
        formula: this.state.formula + '.'
      });
  }
}
}

  render() {
    return (
      <div className="App">
      <div id="calc-container">
        
        <Display 
        formula={this.state.formula} 
        currVal={this.state.currVal} />

        <Buttons 
        evaluate={this.handleEvaluate}
        initialize={this.initialize}
        numbers={this.handleNumbers}
        decimal={this.handleDecimal}
        operators={this.handleOperators}
        />
      </div>
    </div>
    );
  }
}
